___CSS_LOADER_EXPORT___.push([module.id, `.graph-container {
    display: grid;
    gap: 0;
    overflow-x: scroll;
    white-space: nowrap;
    margin-bottom: 80px;
}

.graph-node-row {
    display: flex;
    justify-content: center;
}

.node {
    display: inline-block;
    width: 20px;
    height: 20px;
    outline: 1px solid black;
    background-color: white;
    margin: 1px;
}

.node-start {
    background-color: green;
}

.node-finish {
    background-color: red;
}

.node-test {
    background-color: purple;
}

.node-test2 {
    background-color: purple;
}

.node-test-start {
    background-color: green;
}

.node-queue {
    background-color:rgb(112, 255, 112);
}

.node-wall {
    background-color: rgb(0, 0, 0);
}

.node-weight {
    /* background: url(../../assets/weight.png) center no-repeat;
    background-size: 15px; */
    background-color: rgb(255, 105, 180);
}

.node-visited-color {
    background-color: rgb(112, 128, 144);
}

.node-visited {
    background-color: rgb(112, 128, 144);
    animation-name: visitedAnimation;
    animation-play-state: running;
    animation-fill-mode: forwards;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-duration: 0.2s;
    animation-delay: 0.05s;
    animation-timing-function: ease-in;
}

@keyframes visitedAnimation {
    0% {
        transform: scale(0.9);
        background-color: rgb(169, 169, 169);
    }

    50% {
        transform: scale(0.8);
        background-color: rgb(139, 139, 139);
    }

    75% {
        transform: scale(0.9);
        background-color: rgb(105, 105, 105);
    }

    100% {
        transform: scale(1);
        background-color: rgb(112, 128, 144);
    }
}

.node-shortest-path-color {
    background-color: rgb(255, 255, 0);
}

.node-shortest-path {
    animation-name: shortestPath;
    animation-play-state: running;
    animation-fill-mode: forwards;
    animation-direction: alternate;
    animation-iteration-count: 3;
    animation-duration: 1.2s;
    animation-delay: 0.05s;
    animation-timing-function: ease-in;
}

@keyframes shortestPath {
    0% {
        transform: scale(0.8);
        background-color: rgb(75, 0, 130);
    }

    50% {
        transform: scale(0.9);
        background-color: rgb(255, 215, 0);
    }

    100% {
        transform: scale(1);
        background-color: rgb(255, 255, 0);
    }
}`, "",{"version":3,"sources":["webpack://./src/components/Graph/Graph.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,MAAM;IACN,kBAAkB;IAClB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;IACrB,WAAW;IACX,YAAY;IACZ,wBAAwB;IACxB,uBAAuB;IACvB,WAAW;AACf;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI;4BACwB;IACxB,oCAAoC;AACxC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,oCAAoC;IACpC,gCAAgC;IAChC,6BAA6B;IAC7B,6BAA6B;IAC7B,8BAA8B;IAC9B,4BAA4B;IAC5B,wBAAwB;IACxB,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA;IACI;QACI,qBAAqB;QACrB,oCAAoC;IACxC;;IAEA;QACI,qBAAqB;QACrB,oCAAoC;IACxC;;IAEA;QACI,qBAAqB;QACrB,oCAAoC;IACxC;;IAEA;QACI,mBAAmB;QACnB,oCAAoC;IACxC;AACJ;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,4BAA4B;IAC5B,6BAA6B;IAC7B,6BAA6B;IAC7B,8BAA8B;IAC9B,4BAA4B;IAC5B,wBAAwB;IACxB,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA;IACI;QACI,qBAAqB;QACrB,iCAAiC;IACrC;;IAEA;QACI,qBAAqB;QACrB,kCAAkC;IACtC;;IAEA;QACI,mBAAmB;QACnB,kCAAkC;IACtC;AACJ","sourcesContent":[".graph-container {\n    display: grid;\n    gap: 0;\n    overflow-x: scroll;\n    white-space: nowrap;\n    margin-bottom: 80px;\n}\n\n.graph-node-row {\n    display: flex;\n    justify-content: center;\n}\n\n.node {\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    outline: 1px solid black;\n    background-color: white;\n    margin: 1px;\n}\n\n.node-start {\n    background-color: green;\n}\n\n.node-finish {\n    background-color: red;\n}\n\n.node-test {\n    background-color: purple;\n}\n\n.node-test2 {\n    background-color: purple;\n}\n\n.node-test-start {\n    background-color: green;\n}\n\n.node-queue {\n    background-color:rgb(112, 255, 112);\n}\n\n.node-wall {\n    background-color: rgb(0, 0, 0);\n}\n\n.node-weight {\n    /* background: url(../../assets/weight.png) center no-repeat;\n    background-size: 15px; */\n    background-color: rgb(255, 105, 180);\n}\n\n.node-visited-color 