    display: grid;
    grid-template-columns: 1fr repeat(1, auto) 1fr;
    background: repeating-linear-gradient(45deg, orange, darkviolet, orange);
    background-size: 400%;
    animation: header-animation 3s linear infinite;
}

@keyframes header-animation {
    0% { background-position: 100% 50% }
    100% { background-position: -33% 50% }
}

.header-title {
    background: rgba(235, 235, 235, 0.5);
    padding: 10px;
    grid-column-start: 2;
    font-size: 26px;
    border-radius: 10px;
    outline: 1px solid black;
    margin-top: 15px;
    margin-bottom: 15px;
}

.header-link {
    background: rgba(235, 235, 235, 0.5);
    padding: 5px;
    margin-left: auto;
    margin-right: 10px;
    margin-top: auto;
    margin-bottom: auto;
    border-radius: 5px;
    font-size: 12px;
    outline: 1px solid black;
}`, "",{"version":3,"sources":["webpack://./src/components/Header/Header.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,8CAA8C;IAC9C,wEAAwE;IACxE,qBAAqB;IACrB,8CAA8C;AAClD;;AAEA;IACI,KAAK,8BAA8B;IACnC,OAAO,8BAA8B;AACzC;;AAEA;IACI,oCAAoC;IACpC,aAAa;IACb,oBAAoB;IACpB,eAAe;IACf,mBAAmB;IACnB,wBAAwB;IACxB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,oCAAoC;IACpC,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,wBAAwB;AAC5B","sourcesContent":[".header-container {\n    display: grid;\n    grid-template-columns: 1fr repeat(1, auto) 1fr;\n    background: repeating-linear-gradient(45deg, orange, darkviolet, orange);\n    background-size: 400%;\n    animation: header-animation 3s linear infinite;\n}\n\n@keyframes header-animation {\n    0% { background-position: 100% 50% }\n    100% { background-position: -33% 50% }\n}\n\n.header-title {\n    background: rgba(235, 235, 235, 0.5);\n    padding: 10px;\n    grid-column-start: 2;\n    font-size: 26px;\n    border-radius: 10px;\n    outline: 1px solid black;\n    margin-top: 15px;\n    margin-bottom: 15px;\n}\n\n.header-link {\n    background: rgba(235, 235, 235, 0.5);\n    padding: 5px;\n    margin-left: auto;\n    margin-right: 10px;\n    margin-top: auto;\n    margin-bottom: auto;\n    border-radius: 5px;\n    font-size: 12px;\n    outline: 1px solid black;\n}"],"sourceRoot":""}]);
