    position: absolute;
    display: block;
    background-color: gray;
    text-align: center;
    padding: 8px;
    margin: 0;
    z-index: 1;
}

.dropdown-ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    font-size: 12px;
}

.dropdown-item {
    &:hover {
        background-color: rgb(255, 255, 140);
        color: rgb(33, 0, 33);
    }

    margin-bottom: 4px;
    padding: 4px;
    border-radius: 5px;
